<template>
  <div class="row">
    <div class="col-md-12 mb-3">
      <h1>{{restaurant.name}}</h1>
      <p class="badge badge-secondary mt-1 mb-3">
        {{restaurant.categoryName}}
      </p>
    </div>
    <div class="col-lg-4">
      <img
        class="img-responsive center-block" 
    :src="restaurant.image"
        style="width: 250px;margin-bottom: 25px;"
      >
      <div class="contact-info-wrap">
        <ul class="list-unstyled">
          <li>
            <strong>Opening Hour:</strong>
            {{restaurant.openingHours}}
          </li>
          <li>
            <strong>Tel:</strong>
            {{restaurant.tel}}
          </li>
          <li>
            <strong>Address:</strong>
            {{restaurant.address}}
          </li>
        </ul>
      </div>
    </div>
    <div class="col-lg-8">
      <p>{{restaurant.description}}</p>
      <router-link
        class="btn btn-primary btn-border ms-2"
        :to="{name: 'restaurant-dashbaord', params:{id:restaurant.id}}"
      >Dashboard</router-link>

      <button
        type="button"
        class="btn btn-danger btn-border mx-2"
        v-if="restaurant.isFavorited === true"
        @click.stop.prevent="removeFavorite(restaurant.id)"
      >
        移除最愛
      </button>
      <button
        type="button"
        class="btn btn-primary btn-border mx-2"
        v-else
        @click.stop.prevent="addFavorite(restaurant.id)"
      >
        加到最愛
      </button>
      <button
        type="button"
        class="btn btn-danger like mx-2"
        v-if="restaurant.isLiked === true"
        @click.stop.prevent="removeLike(restaurant.id)"
      >
        Unlike
      </button>
      <button
        type="button"
        class="btn btn-primary like mx-2"
        @click.stop.prevent="addLike(restaurant.id)"
        v-else
      >
        Like
      </button>
    </div>
  </div>
</template>


<script>
import userAPI from './../apis/users'

export default {
    props: {
        initialRestaurant:{
            type:Object,
            required: true
        }
    },
    data(){
        return {
            restaurant: this.initialRestaurant
        }
    },
    methods: {
        async addFavorite(restaurantId) {
          try{
            const {data} = await userAPI.addFavorite({restaurantId})
            if(data.status !== 'success'){
              throw new Error(data.message)
            }
            this.restaurant = {
                ...this.restaurant,
                isFavorited: true
            }

          }
          catch(error){
            console.error(error)
          }
            
        },
        async removeFavorite(restaurantId) {
            try{
            const {data} = await userAPI.deleteFavorite({restaurantId})
            if(data.status !== 'success'){
            throw new Error(data.message)
          }
            this.restaurant = {
                ...this.restaurant,
                isFavorited: false
            }

          }
          catch(error){
            console.error(error)
          }


        },
        async addLike(restaurantId) {
          try{
            const {data} = await userAPI.addLike({restaurantId})
            if(data.status !== 'success'){
            throw new Error(data.message)
          }
            this.restaurant = {
              ...this.restaurant,
              isLiked: true
            }

          }
          catch(error){
            console.error(error)
          }

           
        },
        async removeLike(restaurantId) {
          try{
            const {data} = await userAPI.deleteLike({restaurantId})
            if(data.status !== 'success'){
            throw new Error(data.message)
          }
            this.restaurant = {
                ...this.restaurant,
                isLiked: false
            }

          }
          catch(error){
            console.error(error)
          }

            
        }
    },
    watch:{
      initialRestaurant(newValue){
        this.restaurant = {
          ...this.restaurant,
          ...newValue
        }
      }
    },
  
}
</script>


<style scoped>
.col-lg-8 p,
.contact-info-wrap li,
.contact-info-wrap strong {
  font-family: serif;
  font-size: 17px;
}
</style>